<template>
  <div class="container-fluid">
    <div class="sold-units">
      <h3 class="display-3">Sold Units</h3>
    </div>
    <div class="table-responsive">
      <table class="table table-hover">
        <thead class="thead-dark">
          <tr>
            <th scope="col">#</th>
            <th scope="col">Timer</th>
            <th scope="col">Stock #</th>
            <th scope="col">Sales Associate</th>
            <th scope="col">Deal #</th>
            <th scope="col">Cutomer Name</th>
            <th scope="col">Year</th>
            <th scope="col">Model</th>
            <th scope="col">Customer Care Package</th>
            <th scope="col">Accessories</th>
            <th scope="col">Which Accessories?</th>
            <th scope="col">Approval Status</th>
            <th scope="col"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(req, index) in reqs" :key="index">
            <th>{{index + 1}}</th>
            <th>{{ elapsedTime(req.request_timestamp) }} Min.</th>
            <td>{{ req.stock_number }}</td>
            <td>{{ req.sales_associate }}</td>
            <td>{{ req.deal_number }}</td>
            <td>{{ req.customer_name }}</td>
            <td>{{ req.year }}</td>
            <td>{{ req.model }}</td>
            <td>{{ req.customer_care_package }}</td>
            <td>{{ req.accessories }}</td>
            <td>{{ req.list_accessories }}</td>
            <td>
              <select class="form-control" v-model="req.status_sales" id="status_sales" required>
                <option></option>
                <option v-for="(approval, i) in approvals" :key="i">{{ approval }}</option>
              </select>
            </td>
            <td>
              <button
                @click="updateSalesReq(req.status_sales, req.id, req)"
                class="btn btn-success"
              >Submit</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="we-owes">
      <h3 class="display-3">We Owes</h3>
    </div>
    <div class="table-responsive">
      <table class="table table-hover">
        <thead class="thead-dark">
          <tr>
            <th scope="col">#</th>
            <th scope="col">Timer</th>
            <th scope="col">Sales Associate</th>
            <th scope="col">Customer Name</th>
            <th scope="col">Stock #</th>
            <th scope="col">Type of Vehicle</th>
            <th scope="col">Year</th>
            <th scope="col">Model</th>
            <th scope="col">We Owe Items/Notes</th>
            <th scope="col">Approval Status</th>
            <th scope="col"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(weowe, i) in weowes" :key="i">
            <th>{{i + 1}}</th>
            <th>{{ elapsedTime(weowe.request_timestamp) }} Min.</th>
            <td>{{ weowe.sales_associate }}</td>
            <td>{{ weowe.customer_name }}</td>
            <td>{{ weowe.stock_number }}</td>
            <td>{{ weowe.type_of_vehicle }}</td>
            <td>{{ weowe.year }}</td>
            <td>{{ weowe.model }}</td>
            <td>{{ weowe.we_owe_items }}</td>
            <td>
              <select class="form-control" v-model="weowe.sales_status">
                <option></option>
                <option v-for="(approval, i) in approvals" :key="i">{{ approval }}</option>
              </select>
            </td>
            <td>
              <button
                @click="updateWeOwe(weowe.sales_status, weowe.id, weowe)"
                class="btn btn-success"
              >Submit</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";
import firebase from "firebase";

export default {
  name: "WeOweSales",
  data() {
    return {
      weowes: [],
      reqs: [],
      approvals: ["Approved", "Denied"],
      currentTime: Date.now(),
      state: "started",
      manager: ""
    };
  },
  firestore() {
    return {
      weowes: db
        .collection("weowe")
        .where("sales_status", "==", "Currently Waiting...")
        .orderBy("request_timestamp"),
      reqs: db
        .collection("makeReady")
        .where("status_sales", "==", "Currently Waiting...")
        .orderBy("request_timestamp")
    };
  },
  methods: {
    dateFormat(date) {
      return new Date(date.seconds * 1000).toLocaleString();
    },
    elapsedTime(req) {
      const timestamp = req;
      const startTime = timestamp.seconds * 1000;
      return Math.floor((this.currentTime - startTime) / 60000);
    },
    updateCurrentTime: function() {
      if (this.$data.state == "started") {
        this.currentTime = Date.now();
      }
    },
    updateSalesReq(status, id, req) {
      if (status !== "") {
        if (status === "Approved") {
          if (req.type_of_vehicle === "Pre-owned") {
            db.collection("makeReady")
              .doc(id)
              .update({
                status_sales: "Approved",
                sales_manager: this.manager,
                status_finance: "Currently Waiting...",
                status_carwash: "Currently Waiting...",
                status_parts: "No Parts Needed",
                sales_completion_timestamp: new Date()
              });
          } else {
            db.collection("makeReady")
              .doc(id)
              .update({
                status_sales: "Approved",
                sales_manager: this.manager,
                status_finance: "Currently Waiting...",
                status_carwash: "Currently Waiting...",
                status_parts: "Currently Waiting...",
                sales_completion_timestamp: new Date()
              });
          }
        } else {
          db.collection("makeReady")
            .doc(id)
            .update({
              status_sales: "Denied",
              sales_manager: this.manager,
              sales_completion_timestamp: new Date(),
              status_finance: "N/A",
              status_carWash: "N/A",
              status_parts: "N/A",
              status: "Complete"
            });
        }
      }
    },
    updateWeOwe(status, id, weowe) {
      db.collection("weowe")
        .doc(id)
        .update({
          sales_completion_timestamp: new Date(),
          sales_manager: this.manager,
          sales_status: status
        });
    }
  },
  mounted: function() {
    this.interval = setInterval(this.updateCurrentTime, 1000);
    this.manager = firebase.auth().currentUser.displayName;
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container-fluid {
  margin-top: 20px;
}

.we-owes {
  margin-top: 100px;
}

.display-3 {
  text-align: left;
}
</style>



