<template>
  <div class="container">
    <div class="row">
      <div class="col-md-12">
        <h1 class="display-3 heading">Finance Dispatch</h1>
      </div>
      <div class="card col-md-4" v-for="(req, index) in reqs" :key="index">
        <v-layout @click="delete_request(req.id)" justify-center class="delete mb-2">
          <span class="group">
            <v-icon class="red--text">delete</v-icon>
          </span>
        </v-layout>
        <div class="card-body">
          <h5 class="card-title">
            <span>Stock #:</span>
            {{req.stock_number}}
          </h5>
          <p class="card-text">
            <span>{{ dateFormat(req.sales_completion_timestamp) }}</span>
          </p>
          <p class="card-text" v-if="!req.finance_claimed_timestamp">
            <span>Waiting for: {{ elapsedTime(req.sales_completion_timestamp) }} Minutes</span>
          </p>
          <p class="card-text" v-if="req.finance_claimed_timestamp">
            <span>Claimed for: {{ elapsedTime(req.finance_claimed_timestamp) }} Minutes</span>
          </p>
          <p class="card-text">
            <span>Sales Associate:</span>
            {{req.sales_associate}}
          </p>
          <p class="card-text">
            <span>Deal #:</span>
            {{req.deal_number}}
          </p>
          <p class="card-text">
            <span>Customer Name:</span>
            {{req.customer_name}}
          </p>
          <p class="card-text">
            <span>Type of Vehicle:</span>
            {{req.type_of_vehicle}}
          </p>
          <p class="card-text">
            <span>Mileage:</span>
            {{req.mileage}}
          </p>
          <p
            v-if="req.customer_care_package == 'Yes'"
            class="card-text badge yesccp"
          >Customer Care Package: {{req.customer_care_package}}</p>
          <p
            v-else-if="req.customer_care_package == 'No'"
            class="card-text badge noccp"
          >Customer Care Package: {{req.customer_care_package}}</p>
          <p class="card-text">
            <span>Finanace Associate:</span>
            {{req.finance_associate}}
          </p>
          <p class="card-text">
            <span>Comments:</span>
            {{req.comments}}
          </p>
          <hr />
          <!-- Input fields -->
          <label v-if="req.finance_state" for="status_finance">Status</label>
          <select
            id="status_finance"
            v-if="req.finance_state"
            class="form-control form-control-lg"
            v-model="req.status_finance"
            required
          >
            <option v-for="(approval, index) in approvals" :key="index">{{ approval }}</option>
          </select>
          <label v-if="req.finance_state" for="finance_associate">Finance Associate</label>
          <select
            id="finance_associate"
            v-if="req.finance_state"
            class="form-control form-control-lg"
            v-model="req.finance_associate"
            required
          >
            <option v-for="(finance, index) in finances" :key="index">{{ finance }}</option>
          </select>
          <label v-if="!req.finance_state" for="comments">Comments</label>
          <textarea
            id="comments"
            v-if="!req.finance_state"
            class="form-control form-control-lg"
            v-model="req.comments"
            required
          />
          <v-btn
            v-if="!req.finance_state"
            @click="updateComments(req.id, req.comments)"
            color="success"
          >Update Comments</v-btn>
          <v-btn
            v-if="req.finance_state"
            @click="claimed(req.id, req.finance_associate, req.status_finance)"
            color="success"
          >Claim</v-btn>
          <v-btn outline color="error" v-if="!req.finance_state" @click="unclaimed(req.id)">Unclaim</v-btn>
          <v-btn v-if="!req.finance_state" @click="complete(req, req.id)" color="success">Complete</v-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";
import firebase from "firebase";

export default {
  name: "Finance",
  data() {
    return {
      reqs: [],
      approvals: ["Claimed"],
      finances: [
        "Brad Lilley",
        "Shash Devu",
        "Francis Macias",
        "Kevin Fitzgerald",
        "Carolina Sanchez",
        "Michael Whipple",
        "Miguel Valdez",
        "James Shiflett"
      ],
      currentTime: Date.now(),
      state: "started"
    };
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
        .where("status_sales", "==", "Approved")
        .where("send_to_finance", "==", true)
        .orderBy("sales_completion_timestamp")
    };
  },
  methods: {
    claimed(id, associate, status) {
      db.collection("makeReady")
        .doc(id)
        .update({
          finance_associate: associate,
          finance_claimed_timestamp: new Date(),
          status_finance: status,
          finance_state: false
        });
    },
    updateComments(id, comment) {
      db.collection("makeReady")
        .doc(id)
        .update({
          comments: comment
        });
    },
    unclaimed(id) {
      db.collection("makeReady")
        .doc(id)
        .update({
          finance_associate: "",
          finance_claimed_timestamp: "",
          status_finance: "Currently Waiting...",
          finance_state: true
        });
    },
    complete(req, id) {
      if (
        (req.status_parts == "Complete" ||
          req.status_parts == "No Parts Needed") &&
        req.status_carwash == "Complete"
      ) {
        db.collection("makeReady")
          .doc(id)
          .update({
            finance_completion_timestamp: new Date(),
            status_finance: "Complete",
            send_to_finance: false,
            status: "Complete",
            comments: req.comments
          });
      } else {
        db.collection("makeReady")
          .doc(id)
          .update({
            finance_completion_timestamp: new Date(),
            status_finance: "Complete",
            send_to_finance: false
          });
      }
    },
    dateFormat(date) {
      return new Date(date.seconds * 1000).toLocaleString();
    },
    delete_request(id) {
      if (this.manager == "James Shiflett" || this.manager == "Austin Hughes") {
        db.collection("makeReady")
          .doc(id)
          .update({
            license_plate: "",
            parts_associate: "",
            status_parts: "",
            parts_completion_timestamp: "",
            parts_claimed_timestamp: "",
            send_to_parts: "",
            invoice_number: "",
            mats: "",
            mats_shelf: "",
            parts_state: "",
            finance_completion_timestamp: "",
            finance_claimed_timestamp: "",
            finance_associate: "",
            status_finance: "",
            send_to_finance: "",
            finance_state: "",
            carwash_claimed_timestamp: "",
            carwash_complete_timestamp: "",
            status_carwash: "",
            delivery_bay: "",
            checked_mats: "",
            carwash_associate: "",
            send_to_carwash: "",
            carwash_state: "",
            license_plate_installed: "",
            sales_completion_timestamp: "",
            status_sales: "'",
            sales_manager: "",
            delete_timestamp: new Date(),
            delete_status: true,
            status: "deleted"
          });
      } else {
        alert(
          "Please speak with either James or Austin to get this request deleted! Thanks!"
        );
      }
    },
    elapsedTime(time) {
      const timestamp = time;
      const startTime = timestamp.seconds * 1000;
      return Math.floor((this.currentTime - startTime) / 60000);
    },
    updateCurrentTime: function() {
      if (this.$data.state == "started") {
        this.currentTime = Date.now();
      }
    }
  },
  mounted() {
    this.financeAssociate = firebase.auth().currentUser.displayName;
    this.interval = setInterval(this.updateCurrentTime, 1000);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  margin-top: 20px;
  margin-bottom: 30px;
}

button {
  margin-top: 15px;
}

.container-fluid {
  width: 90%;
}

.heading {
  margin-bottom: 5rem;
  margin-top: 5rem;
}

.card-title {
  font-weight: bold;
  font-size: 1.6rem;
}

span {
  font-weight: bold;
}

label {
  font-weight: bold;
}
.group {
  margin-top: 10px;
}
</style>



