// Dependencies
import Vue from "vue";
import Router from "vue-router";
import firebase from "firebase";
import { db } from "../main";
// Components
import Request from "@/components/Request";
import Newcar from "@/components/Newcar";
import Loaner from "@/components/Loaner";
import Preowned from "@/components/Preowned";
import Finance from "@/components/Finance";
import Carwash from "@/components/Carwash";
import Parts from "@/components/Parts";
import Metrics from "@/components/Metrics";
import SuccessfulSubmission from "@/components/SuccessfulSubmission";
import SignIn from "@/components/SignIn";
import Bugs from "@/components/Bugs";
import SubmitBug from "@/components/SubmitBug";
import LastComplete from "@/components/LastComplete";
import LastDenied from "@/components/LastDenied";
import Oops from "@/components/Oops";
import SalesManager from "@/components/SalesManager";
import Unidentified from "@/components/Unidentified";
import MainReport from "@/components/MainReport";
import WeOwe from "@/components/WeOwe";
import WeOweDashboard from "@/components/WeOweDashboard";
import WeOweParts from "@/components/WeOweParts";
import WeOweSales from "@/components/WeOweSales";

// Init Vue Router
Vue.use(Router);

// Defining routes
const router = new Router({
  routes: [
    {
      path: "/",
      name: "Request",
      component: Request,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/newcar",
      name: "SalesManager",
      component: Newcar,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/loancar",
      name: "SalesManager",
      component: Loaner,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/preowned",
      name: "SalesManager",
      component: Preowned,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/finance",
      name: "Finance",
      component: Finance,
      access: "Finance",
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/Detail",
      name: "Detail",
      component: Carwash,
      access: "Detail",
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/parts",
      name: "Parts",
      component: Parts,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/metrics",
      name: "Metrics",
      component: Metrics,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/submission",
      name: "SuccessfulSubmission",
      component: SuccessfulSubmission,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/signin",
      name: "SignIn",
      component: SignIn
    },
    {
      path: "/bugs",
      name: "Bugs",
      component: Bugs,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/submitbug",
      name: "SubmitBug",
      component: SubmitBug,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/lastcomplete",
      name: "LastComplete",
      component: LastComplete,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/lastdenied",
      name: "LastDenied",
      component: LastDenied,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/oops",
      name: "Oops",
      component: Oops
    },
    {
      path: "/salesmanager",
      name: "SalesManager",
      component: SalesManager,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/unidentified",
      name: "Unidentified",
      component: Unidentified
    },
    {
      path: "/mainreport",
      name: "MainReport",
      component: MainReport,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/weowe",
      name: "WeOwe",
      component: WeOwe,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/weowedashboard",
      name: "WeOweDashboard",
      component: WeOweDashboard,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/weoweparts",
      name: "WeOweParts",
      component: WeOweParts,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/weowesales",
      name: "WeOweSales",
      component: WeOweSales,
      meta: {
        requiresAuth: true
      }
    }
  ]
});

// Router Guards
router.beforeEach((to, from, next) => {
  let cred = firebase.auth().currentUser;
  if (to.matched.some(rec => rec.meta.requiresAuth)) {
    if (cred) {
      db.collection("users")
        .doc(cred.email)
        .get()
        .then(doc => {
          // check auth state of user
          if (
            doc.data().access_level == to.name ||
            to.name == "Metrics" ||
            to.name == "LastComplete" ||
            to.name == "LastDenied" ||
            to.name == "Bugs" ||
            to.name == "SubmitBug"
          ) {
            next();
          } else if (
            doc.data().access_level == "SalesManager" &&
            to.name != "Detail" &&
            to.name != "Finance" &&
            to.name != "Parts"
          ) {
            next();
          } else if (doc.data().access_level == "Admin") {
            next();
          } else if (
            doc.data().access_level == "Sales" &&
            to.name != "Detail" &&
            to.name != "Finance" &&
            to.name != "Parts" &&
            to.name != "SalesManager"
          ) {
            next();
          } else if (doc.data().access_level == "Unidentified") {
            next("/unidentified");
          } else {
            next("/oops");
          }
          // User is signed in. Proceed to route
        });
    } else {
      next("/signin");
    }
  } else {
    next();
  }

  // check to see if route has auth guard
});
export default router;



