<template>
  <div class="container">
    <div class="row align-content-center justify-content-center">
      <h3 class="display-3 sm-auto col-lg-12">Welcome to Make Ready</h3>
      <h4 class="sm-auto col-lg-12">Sign in with Your Principle Auto Google Account</h4>
      <button class="btn btn-outline-dark btn-lg sm-auto" @click.prevent="signup">
        <img
          alt="Google &quot;G&quot; Logo"
          src="https://upload.wikimedia.org/wikipedia/commons/thumb/5/53/Google_%22G%22_Logo.svg/512px-Google_%22G%22_Logo.svg.png"
        />
        <span>Sign In With Google</span>
      </button>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";
import firebase from "firebase";

export default {
  name: "SignUp",
  data() {
    return {
      reqs: [],
      email: "",
      pass: "",
      user: "",
      test: ""
    };
  },
  methods: {
    // this is google sign in. I can get a log in, need to figure out how to know people are logged in and how to keep them logged in. Also how to send the information to other components.
    signup() {
      const provider = new firebase.auth.GoogleAuthProvider();

      firebase
        .auth()
        .signInWithPopup(provider)
        .then(cred => {
          let ref = db.collection("users").doc(cred.user.email);
          ref.get().then(doc => {
            if (doc.exists) {
              console.log("This user has an account. They are logged in now.");
              let access = doc.data().access_level;
              if (access == "Sales") {
                this.$router.push("/");
              } else {
                this.$router.push("/" + access);
              }
            } else {
              console.log(cred);
              ref.set({
                display_name: cred.user.displayName,
                user_id: cred.user.uid,
                email: cred.user.email,
                is_verified: cred.user.emailVerified,
                access_level: "Unidentified"
              });

              this.$router.push("/unidentified");
            }
          });
        })
        .catch(err => {
          alert("Oops. " + err.message);
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
button {
  margin-top: 30px;
}
span {
  margin-left: 10px;
}

img {
  max-width: 30px;
}

.row {
  height: 500px;
}
</style>



