<template>
  <div class="container">
    <div class="row">
      <div class="col-md-12">
        <h1 class="display-3 heading">Bug Report</h1>
      </div>
      <div class="col-md-12 bug-submit">
        <h3 class="found-bug">Found a bug? Go ahead, submit it so we can fix it!</h3>
        <v-btn to="/submitbug" color="success">Submit a Bug</v-btn>
      </div>
      <table class="table table-hover table-dark">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Id</th>
            <th scope="col">Assocaite Name</th>
            <th scope="col">Comments</th>
            <th scope="col">Status</th>
            <th scope="col">Status Comments</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(bug, index) in bugs" :key="index">
            <th>{{ index + 1 }}</th>
            <td>{{bug.id}}</td>
            <td>{{ bug.associate_name }}</td>
            <td>{{ bug.comments }}</td>
            <td>{{ bug.status }}</td>
            <td>{{bug.status_comments}}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";

export default {
  name: "Bugs",
  data() {
    return {
      bugs: []
    };
  },
  firestore() {
    return {
      bugs: db.collection("bug_report")
    };
  }
};
</script>

<style scoped>
table {
  table-layout: fixed;
}
td {
  word-wrap: break-word;
}

.found-bug {
  margin-bottom: 20px;
}

.bug-submit {
  margin-bottom: 30px;
}
</style>





