<template>
  <div class="container">
    <div class="row">
      <div class="col-md-12">
        <h1 class="display-3 heading">Loan Car Dispatch</h1>
      </div>
      <div class="card" v-for="(req, index) in reqs" :key="index">
        <v-layout @click="delete_request(req.id)" justify-center class="delete mb-2">
          <span class="group">
            <v-icon class="red--text">delete</v-icon>
          </span>
        </v-layout>
        <div class="card-body">
          <h5 class="card-title">
            <span>Stock #:</span>
            {{req.stock_number}}
          </h5>
          <p class="card-text">
            <span>Sales Associate:</span>
            {{req.sales_associate}}
          </p>
          <p class="card-text">
            <span>Deal #:</span>
            {{req.deal_number}}
          </p>
          <p class="card-text">
            <span>Customer Name:</span>
            {{req.customer_name}}
          </p>
          <p class="card-text">
            <span>Year:</span>
            {{req.year}}
          </p>
          <p class="card-text">
            <span>Model:</span>
            {{req.model}}
          </p>
          <p class="card-text">
            <span>Detail Service:</span>
            {{req.detail_service}}
          </p>
          <p
            v-if="req.customer_care_package == 'Yes'"
            class="card-text badge yesccp"
          >Customer Care Package: {{req.customer_care_package}}</p>
          <p
            v-else-if="req.customer_care_package == 'No'"
            class="card-text badge noccp"
          >Customer Care Package: {{req.customer_care_package}}</p>
          <p
            v-if="req.accessories == 'Yes'"
            class="card-text badge yesccp"
          >Accessories: {{req.accessories}}</p>
          <p
            v-else-if="req.accessories == 'No'"
            class="card-text badge noccp"
          >Accessories: {{req.accessories}}</p>
          <p class="card-text" v-if="req.accessories == 'Yes'">
            <span>List Accessories:</span>
            {{req.list_accessories}}
          </p>
          <hr />
          <form @submit.prevent="updateSalesReq(req.status_sales,req.id)">
            <label for="status_sales">Approval Status</label>
            <select
              id="status_sales"
              class="form-control form-control-lg"
              v-model="req.status_sales"
              required
            >
              <option v-for="(approval, index) in approvals" :key="index">{{ approval }}</option>
            </select>
            <v-btn color="success" type="submit">Submit</v-btn>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";
import firebase from "firebase";

export default {
  name: "New",
  data() {
    return {
      reqs: [],
      approvals: ["Approved", "Denied"],
      manager: ""
    };
  },
  mounted() {
    this.manager = firebase.auth().currentUser.displayName;
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
        .where("type_of_vehicle", "==", "Loaner")
        .where("status_sales", "==", "Currently Waiting...")
    };
  },
  methods: {
    updateSalesReq(status, id) {
      if (status !== "") {
        if (status === "Approved") {
          db.collection("makeReady")
            .doc(id)
            .update({
              status_sales: "Approved",
              sales_manager: this.manager,
              status_finance: "Currently Waiting...",
              status_carwash: "Currently Waiting...",
              status_parts: "Currently Waiting...",
              sales_completion_timestamp: new Date()
            });
        } else {
          db.collection("makeReady")
            .doc(id)
            .update({
              status_sales: "Denied",
              sales_manager: this.manager,
              sales_completion_timestamp: new Date(),
              status: "Complete"
            });
        }
      }
    },
    delete_request(id) {
      if (this.manager == "James Shiflett" || this.manager == "Austin Hughes") {
        db.collection("makeReady")
          .doc(id)
          .update({
            license_plate: "",
            parts_associate: "",
            status_parts: "",
            parts_completion_timestamp: "",
            parts_claimed_timestamp: "",
            send_to_parts: "",
            invoice_number: "",
            mats: "",
            mats_shelf: "",
            parts_state: "",
            finance_completion_timestamp: "",
            finance_claimed_timestamp: "",
            finance_associate: "",
            status_finance: "",
            send_to_finance: "",
            finance_state: "",
            carwash_claimed_timestamp: "",
            carwash_complete_timestamp: "",
            status_carwash: "",
            delivery_bay: "",
            checked_mats: "",
            carwash_associate: "",
            send_to_carwash: "",
            carwash_state: "",
            license_plate_installed: "",
            sales_completion_timestamp: "",
            status_sales: "'",
            sales_manager: "",
            delete_timestamp: new Date(),
            delete_status: true,
            status: "deleted"
          });
      } else {
        alert(
          "Please speak with either James or Austin to get this request deleted! Thanks!"
        );
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  margin-top: 20px;
  margin-bottom: 30px;
}

button {
  margin-top: 20px;
}

.btn-primary {
  background-color: #0096a5;
  border-color: #0096a5;
}

.container-fluid {
  width: 90%;
}

.alertStyle {
  position: fixed; /* Sit on top of the page content */
  width: 100%; /* Full width (cover the whole page) */
  height: 10%; /* Full height (cover the whole page) */
  padding-top: 40px;
  margin: auto 0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
  cursor: pointer; /* Add a pointer on hover */
  vertical-align: middle;
}

.heading {
  margin-bottom: 5rem;
  margin-top: 5rem;
}

.card-title,
.card-title span {
  font-weight: bold;
  font-size: 1.6rem;
}

.card-title {
  font-weight: bold;
  font-size: 1.6rem;
}

span {
  font-weight: bold;
  font-size: 1.1rem;
}

label {
  font-weight: bold;
  font-size: 1.1rem;
}

form label {
  margin-top: 10px;
}

p {
  font-size: 1.2rem;
}
.group {
  margin-top: 10px;
}
</style>



