<template>
  <table class="table">
    <thead class="thead-dark">
      <tr>
        <th scope="col">#</th>
        <th scope="col">Id</th>
        <th scope="col">Sales Associate</th>
        <th scope="col">Stock #</th>
        <th scope="col">Sales Status</th>
        <th scope="col">Parts Status</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(wo, index) in weowes" :key="index">
        <th>{{index + 1}}</th>
        <td>{{ wo.id }}</td>
        <td>{{ wo.sales_associate }}</td>
        <td>{{ wo.stock_number }}</td>
        <td>{{ wo.sales_status }}</td>
        <td>{{ wo.parts_status }}</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import { db } from "../main";
import router from "../router";
import firebase from "firebase";

export default {
  name: "WeOweDashboard",
  data() {
    return {
      weowes: [],
      currentTime: Date.now(),
      state: "started"
    };
  },
  firestore() {
    return {
      weowes: db.collection("weowe").where("status", "==", "Incomplete")
    };
  },
  methods: {
    dateFormat(date) {
      return new Date(date.seconds * 1000).toLocaleString();
    },
    elapsedTime(req) {
      const timestamp = req.sales_completion_timestamp;
      const startTime = timestamp.seconds * 1000;
      return Math.floor((this.currentTime - startTime) / 60000);
    },
    updateCurrentTime: function() {
      if (this.$data.state == "started") {
        this.currentTime = Date.now();
      }
    }
  },
  mounted: function() {
    this.interval = setInterval(this.updateCurrentTime, 1000);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.approved {
  background-color: green;
}

.inprogress {
  background-color: yellow;
}

.denied {
  background-color: red;
}

span {
  font-weight: bold;
  font-size: 15px;
}
</style>



