<template>
  <div>
    <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
      <div class="container-fluid" id="nav-background">
        <router-link class="navbar-brand" to="/">Make Ready | BMW of SA</router-link>
        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarSupportedContent">
          <ul class="navbar-nav ml-auto">
            <hr v-if="user" />
            <button v-if="user">
              <span class="username">
                Logged in as
                <span class="user">{{ user.displayName }}</span>
              </span>
            </button>

            <hr v-if="user" />
            <router-link
              v-if="user && accesslevel != 'Parts' && accesslevel != 'Finance' && accesslevel != 'Detail' "
              class="nav-link active"
              to="/"
            >Request</router-link>
            <li v-if="user && accesslevel != 'Sales' " class="nav-item dropdown">
              <a
                class="active nav-link dropdown-toggle"
                id="navbarDropdown"
                role="button"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >Departments</a>
              <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                <router-link
                  :to="dep.path"
                  v-if="(dep.access == accesslevel) || (accesslevel == 'Admin' ) "
                  v-for="(dep, i) in departments"
                  :key="i"
                  class="dropdown-item"
                >
                  <div>
                    <span>{{ dep.title }}</span>
                  </div>
                </router-link>
              </div>
            </li>
            <router-link v-if="!user" to="/signin" class="active nav-link ml-sm-2">Sign In</router-link>
            <li v-if="user" class="nav-item dropdown">
              <a
                class="active nav-link dropdown-toggle"
                id="navbarDropdown"
                role="button"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >Dashboards</a>
              <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                <router-link
                  v-for="(dashboard, i) in dashboards"
                  :key="i"
                  :to="dashboard.path"
                  class="dropdown-item"
                >
                  <span>{{ dashboard.title }}</span>
                </router-link>
              </div>
            </li>
            <router-link v-if="user" class="nav-link active" to="/bugs">Bugs</router-link>
            <button @click="logout()" v-if="user" class="nav-link active">Log Out</button>
          </ul>
        </div>
      </div>
    </nav>
  </div>
</template>
<script>
import firebase from "firebase";
import { db } from "../main";

export default {
  name: "Navbar",
  data() {
    return {
      user: "",
      accesslevel: "",
      profile: "",
      test: "",
      dashboards: [
        {
          title: "In Progress",
          path: "/metrics"
        },
        {
          title: "Last 50 Complete",
          path: "/lastcomplete"
        },
        {
          title: "Last 50 Denied",
          path: "lastdenied"
        }
      ],
      departments: [
        {
          title: "New Car",
          path: "/newcar",
          access: "SalesManager"
        },
        {
          title: "TPO",
          path: "/preowned",
          access: "SalesManager"
        },
        {
          title: "Loan Car",
          path: "/loancar",
          access: "SalesManager"
        },
        {
          title: "Parts",
          path: "/parts",
          access: "Parts"
        },
        {
          title: "Finance",
          path: "/finance",
          access: "Finance"
        },
        {
          title: "Detail",
          path: "/detail",
          access: "Detail"
        }
      ]
    };
  },
  methods: {
    toggleDrawer() {
      if (this.drawer.permanent) {
        this.drawer.permanent = !this.drawer.permanent;
        // set the clipped state of the drawer and toolbar
        this.drawer.clipped = true;
        this.toolbar.clippedLeft = true;
      } else {
        // normal drawer
        this.drawer.open = !this.drawer.open;
      }
    },
    logout() {
      firebase.auth().signOut();
      this.$router.push("/signin");
    }
  },
  created() {
    // let user = firebase.auth().currentUser
    firebase.auth().onAuthStateChanged(cred => {
      if (cred) {
        this.user = cred;
        this.profile = db
          .collection("users")
          .where("email", "==", this.user.email)
          .get()
          .then(snapshot => {
            this.accesslevel =
              snapshot.docs[0]._document.proto.fields.access_level.stringValue;
            console.log(
              snapshot.docs[0]._document.proto.fields.access_level.stringValue
            );
          });
      } else {
        this.user = null;
      }
    });
  }
};
</script>

<style scoped>
.v-navigation-drawer > .v-list .v-list__tile__title {
  height: 60px;
}
#nav-background {
  padding: 5px;
}
button {
  color: white;
}
hr {
  border-color: white;
}

.username {
  font-size: 1em;
  color: white;
}

.user {
  font-weight: bold;
}
</style>





