<template>
  <div class="container">
    <div class="row">
      <div class="col-md-12">
        <h1 class="display-3 heading">Detail Dispatch</h1>
      </div>
      <div class="card col-md-4" v-for="(req, index) in reqs" :key="index">
        <v-layout @click="delete_request(req.id)" justify-center class="delete mb-2">
          <span class="group">
            <v-icon large class="white--text">delete</v-icon>
          </span>
        </v-layout>
        <div class="card-body">
          <h5 class="card-title">
            <span>Stock #:</span>
            {{req.stock_number}}
          </h5>
          <p class="card-text">
            <span>Sales Associate:</span>
            {{req.sales_associate}}
          </p>
          <p class="card-text">
            <span>Customer Name:</span>
            {{req.customer_name}}
          </p>
          <p class="card-text">
            <span>Type of Vehicle:</span>
            {{req.type_of_vehicle}}
          </p>
          <p class="card-text">
            <span>Year:</span>
            {{req.year}}
          </p>
          <p class="card-text">
            <span>Model:</span>
            {{req.model}}
          </p>
          <p class="card-text">
            <span>Detail Service:</span>
            {{req.detail_service}}
          </p>
          <p class="card-text">
            <span>License Plate Bracket:</span>
            {{req.license_plate}}
          </p>
          <p class="card-text">
            <span>Detail Associate:</span>
            {{req.carwash_associate}}
          </p>
          <hr />
          <!-- input fields -->
          <form action>
            <label v-if="req.carwash_state" for="status_carwash">Status</label>
            <select
              id="status_carwash"
              v-if="req.carwash_state"
              class="form-control form-control-lg"
              v-model="req.status_carwash"
              required
            >
              <option v-for="(approval, index) in approvals" :key="index">{{ approval }}</option>
            </select>
            <div v-if="!req.carwash_state">
              <label>Are the mats in the car?</label>
              <select
                id="carwash_associate"
                class="form-control form-control-lg"
                v-model="req.checked_mats"
                required
              >
                <option v-for="(mat, index) in mats" :key="index">{{ mat }}</option>
              </select>
            </div>
          </form>
        </div>
        <div v-if="!req.carwash_state">
          <div v-if="!req.carwash_state && req.license_plate =='Yes'">
            <form>
              <label>Is the license plate bracket installed?</label>
              <select
                id="carwash_associate"
                class="form-control form-control-lg"
                v-model="req.license_plate_installed"
                required
              >
                <option v-for="(mat, index) in mats" :key="index">{{ mat }}</option>
              </select>
            </form>
          </div>
        </div>
        <form>
          <label v-if="!req.carwash_state" for="delivery_bay">Delivery Bay</label>
          <select
            v-if="!req.carwash_state"
            id="delivery_bay"
            class="form-control form-control-lg"
            v-model="req.delivery_bay"
            required
          >
            <option v-for="(del, index) in delivery" :key="index">{{ del }}</option>
          </select>
          <v-btn
            v-if="req.carwash_state"
            @click="claimed(req.id, req.status_carwash)"
            color="success"
          >Claim</v-btn>
          <v-btn outline color="error" v-if="!req.carwash_state" @click="unclaimed(req.id)">Unclaim</v-btn>
          <v-btn
            v-if="!req.carwash_state"
            @click="complete(req, req.id, req.checked_mats, req.license_plate_installed, req.delivery_bay)"
            color="success"
          >Complete</v-btn>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";
import firebase from "firebase";

export default {
  name: "Detail",
  data() {
    return {
      reqs: [],
      approvals: ["Claimed"],
      delivery: ["Bay 1", "Bay 2", "Bay 3", "Bay 4", "Delivery Bay Overflow"],
      mats: ["Yes", "No"],
      completionSales: "",
      salesManager: "",
      claimedStatus: "",
      carwashassociate: "",
      delivery_bay: "",
      detailAssociate: ""
    };
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
        .where("status_sales", "==", "Approved")
        .where("send_to_carwash", "==", true)
    };
  },
  methods: {
    claimed(id, status) {
      db.collection("makeReady")
        .doc(id)
        .update({
          carwash_associate: this.detailAssociate,
          carwash_claimed_timestamp: new Date(),
          status_carwash: "Claimed",
          carwash_state: false
        });
    },
    unclaimed(id) {
      db.collection("makeReady")
        .doc(id)
        .update({
          carwash_associate: "",
          carwash_claimed_timestamp: "",
          status_carwash: "Currently Waiting...",
          carwash_state: true
        });
    },
    complete(req, id, checked_mats, license_plate_installed, delivery_bay) {
      if (
        (req.status_parts == "Complete" ||
          req.status_parts == "No Parts Needed") &&
        req.status_finance == "Complete"
      ) {
        db.collection("makeReady")
          .doc(id)
          .update({
            status_carwash: "Complete",
            carwash_complete_timestamp: new Date(),
            delivery_bay: delivery_bay,
            checked_mats: checked_mats,
            license_plate_installed: license_plate_installed,
            send_to_carwash: false,
            status: "Complete"
          });
      } else {
        db.collection("makeReady")
          .doc(id)
          .update({
            status_carwash: "Complete",
            carwash_complete_timestamp: new Date(),
            delivery_bay: delivery_bay,
            checked_mats: checked_mats,
            license_plate_installed: license_plate_installed,
            send_to_carwash: false
          });
      }
    },
    delete_request(id) {
      db.collection("makeReady")
        .doc(id)
        .update({
          license_plate: "",
          parts_associate: "",
          status_parts: "",
          parts_completion_timestamp: "",
          parts_claimed_timestamp: "",
          send_to_parts: "",
          invoice_number: "",
          mats: "",
          mats_shelf: "",
          parts_state: "",
          finance_completion_timestamp: "",
          finance_claimed_timestamp: "",
          finance_associate: "",
          status_finance: "",
          send_to_finance: "",
          finance_state: "",
          carwash_claimed_timestamp: "",
          carwash_complete_timestamp: "",
          status_carwash: "",
          delivery_bay: "",
          checked_mats: "",
          carwash_associate: "",
          send_to_carwash: "",
          carwash_state: "",
          license_plate_installed: "",
          sales_completion_timestamp: "",
          status_sales: "'",
          sales_manager: "",
          password: "",
          delete_timestamp: new Date(),
          delete_status: true,
          delete_associate: person,
          status: "deleted"
        });
    }
  },
  mounted() {
    this.detailAssociate = firebase.auth().currentUser.displayName;
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  margin-top: 20px;
  margin-bottom: 30px;
}

button {
  margin-top: 20px;
}

.btn-primary {
  background-color: #0096a5;
  border-color: #0096a5;
}

.container-fluid {
  width: 90%;
}

.alertStyle {
  position: fixed; /* Sit on top of the page content */
  width: 100%; /* Full width (cover the whole page) */
  height: 10%; /* Full height (cover the whole page) */
  padding-top: 40px;
  margin: auto 0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
  cursor: pointer; /* Add a pointer on hover */
  vertical-align: middle;
}

.heading {
  margin-bottom: 5rem;
  margin-top: 5rem;
}

.card-title,
.card-title span {
  font-weight: bold;
  font-size: 1.6rem;
}

.card-title {
  font-weight: bold;
  font-size: 1.6rem;
}

span {
  font-weight: bold;
  font-size: 1.1rem;
}

label {
  font-weight: bold;
  font-size: 1.1rem;
  margin-top: 10px;
}

p {
  font-size: 1.2rem;
}

.delete {
  opacity: 0;
  cursor: pointer;
  background-color: #e46868;
}

.delete:hover {
  opacity: 100;
}
</style>



