<template>
  <div class="container">
    <div class="row">
      <div class="col-md-12">
        <h1 class="display-3 heading">Parts Dispatch</h1>
      </div>
      <div class="card" v-for="(req, index) in reqs" :key="index">
        <v-layout @click="delete_request(req.id)" justify-center class="delete mb-2">
          <span class="group">
            <v-icon class="red--text">delete</v-icon>
          </span>
        </v-layout>
        <div class="card-body">
          <h5 class="card-title">
            <span>Stock #:</span>
            {{req.stock_number}}
          </h5>
          <p class="card-text">
            <span>Sales Associate:</span>
            {{req.sales_associate}}
          </p>
          <p class="card-text">
            <span>Customer Name:</span>
            {{req.customer_name}}
          </p>
          <p class="card-text">
            <span>Year:</span>
            {{req.year}}
          </p>
          <p class="card-text">
            <span>Model:</span>
            {{req.model}}
          </p>
          <p class="card-text">
            <span>Type of Vehicle:</span>
            {{req.type_of_vehicle}}
          </p>
          <p
            v-if="req.customer_care_package == 'Yes'"
            class="card-text badge yesccp"
          >Customer Care Package: {{req.customer_care_package}}</p>
          <p
            v-else-if="req.customer_care_package == 'No'"
            class="card-text badge noccp"
          >Customer Care Package: {{req.customer_care_package}}</p>
          <p
            v-if="req.accessories == 'Yes'"
            class="card-text badge yesccp"
          >Accessories: {{req.accessories}}</p>
          <p
            v-else-if="req.accessories == 'No'"
            class="card-text badge noccp"
          >Accessories: {{req.accessories}}</p>
          <p class="card-text" v-if="req.accessories == 'Yes'">
            <span>List Accessories:</span>
            {{req.list_accessories}}
          </p>
          <p class="card-text">
            <span>Parts Associate:</span>
            {{req.parts_associate}}
          </p>
          <p class="card-text">
            <span>Additional Information:</span>
            {{req.additional_comments}}
          </p>
          <hr />
          <!-- *****************************
                      Input fields 
          ********************************-->
          <label v-if="req.parts_state" for="status_parts">Status</label>
          <select
            id="status_parts"
            v-if="req.parts_state"
            class="form-control form-control-lg"
            v-model="req.status_parts"
            required
          >
            <option v-for="(approval, index) in approvals" :key="index">{{ approval }}</option>
          </select>

          <label
            v-if="!req.parts_state && req.customer_care_package == 'No' "
            for="mats"
          >Was this an after hours request?</label>
          <select
            v-if="!req.parts_state && req.customer_care_package == 'No' "
            id="mats"
            class="form-control form-control-lg"
            v-model="req.afterhours"
          >
            <option v-for="(approva, index) in approval" :key="index">{{ approva }}</option>
          </select>

          <div
            v-if="!req.parts_state && req.customer_care_package == 'No' && req.afterhours == 'Yes' "
          >
            <label for="mats">Mats</label>
            <select id="mats" class="form-control form-control-lg" v-model="req.mats">
              <option>Carpet Mats</option>
            </select>
          </div>

          <div v-else-if="!req.parts_state && req.afterhours == 'No'">
            <label for="mats">Mats</label>
            <select id="mats" class="form-control form-control-lg" v-model="req.mats">
              <option v-for="(mat, index) in mats" :key="index">{{ mat }}</option>
            </select>
          </div>

          <div v-else-if="!req.parts_state">
            <label for="mats">Mats</label>
            <select id="mats" class="form-control form-control-lg" v-model="req.mats">
              <option v-for="(mat, i) in mats" :key="i">{{ mat }}</option>
            </select>
          </div>

          <div v-if="!req.parts_state">
            <label for="parts_on_order">Did Parts Need to Be Ordered?</label>
            <select
              id="parts_on_order"
              class="form-control form-control-lg"
              v-model="req.parts_on_order"
            >
              <option v-for="(approva, i) in approval" :key="i">{{ approva }}</option>
            </select>
          </div>

          <hr v-if="!req.parts_state" />

          <label v-if="req.mats == 'Rubber Mats (CCP)'" for="mats">Invoice #</label>
          <input
            v-if="req.mats == 'Rubber Mats (CCP)'"
            type="text"
            class="form-control form-control-lg"
            id="stock_number"
            placeholder="ex: 123456"
            v-model="req.invoice_number"
            required
          />

          <v-btn
            v-if="req.parts_state"
            @click="claimed(req.id, req.status_parts)"
            color="success"
          >Claim</v-btn>
          <v-btn outline color="error" v-if="!req.parts_state" @click="unclaimed(req.id)">Unclaim</v-btn>
          <v-btn
            v-if="!req.parts_state"
            @click="complete(req, req.id, req.afterhours)"
            color="success"
          >Complete</v-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";
import firebase from "firebase";

export default {
  name: "Parts",
  data() {
    return {
      reqs: [],
      approvals: ["Claimed"],
      mats: ["Rubber Mats (CCP)", "Carpet Mats"],
      approval: ["Yes", "No"],
      partsAssociate: ""
    };
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
        .where("send_to_parts", "==", true)
        .where("status_sales", "==", "Approved")
    };
  },
  methods: {
    claimed(id, status) {
      db.collection("makeReady")
        .doc(id)
        .update({
          parts_associate: this.partsAssociate,
          parts_claimed_timestamp: new Date(),
          status_parts: status,
          parts_state: false
        });
    },
    unclaimed(id) {
      db.collection("makeReady")
        .doc(id)
        .update({
          parts_associate: "",
          parts_claimed_timestamp: "",
          status_parts: "Currently Waiting...",
          parts_state: true
        });
    },
    complete(req, id) {
      if (
        req.status_finance == "Complete" &&
        req.status_carwash == "Complete"
      ) {
        db.collection("makeReady")
          .doc(id)
          .update({
            invoice_number: req.invoice_number,
            parts_completion_timestamp: new Date(),
            status_parts: "Complete",
            mats: req.mats,
            send_to_parts: false,
            parts_on_order: req.parts_on_order,
            status: "Complete"
          });
      } else {
        db.collection("makeReady")
          .doc(id)
          .update({
            invoice_number: req.invoice_number,
            parts_completion_timestamp: new Date(),
            status_parts: "Complete",
            mats: req.mats,
            send_to_parts: false,
            parts_on_order: req.parts_on_order
          });
      }
    },
    delete_request(id) {
      if (
        this.partsAssociate == "James Shiflett" ||
        this.partsAssociate == "Austin Hughes"
      ) {
        db.collection("makeReady")
          .doc(id)
          .update({
            license_plate: "",
            parts_associate: "",
            status_parts: "",
            parts_completion_timestamp: "",
            parts_claimed_timestamp: "",
            send_to_parts: "",
            invoice_number: "",
            mats: "",
            mats_shelf: "",
            parts_state: "",
            finance_completion_timestamp: "",
            finance_claimed_timestamp: "",
            finance_associate: "",
            status_finance: "",
            send_to_finance: "",
            finance_state: "",
            carwash_claimed_timestamp: "",
            carwash_complete_timestamp: "",
            status_carwash: "",
            delivery_bay: "",
            checked_mats: "",
            carwash_associate: "",
            send_to_carwash: "",
            carwash_state: "",
            license_plate_installed: "",
            sales_completion_timestamp: "",
            status_sales: "'",
            sales_manager: "",
            delete_timestamp: new Date(),
            delete_status: true,
            status: "deleted"
          });
      } else {
        alert(
          "Please speak with either James or Austin to get this request deleted! Thanks!"
        );
      }
    }
  },
  mounted() {
    this.partsAssociate = firebase.auth().currentUser.displayName;
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  margin-top: 20px;
  margin-bottom: 30px;
}

button {
  margin-top: 15px;
}

.container-fluid {
  width: 90%;
}

.heading {
  margin-bottom: 5rem;
  margin-top: 5rem;
}

.card-title {
  font-weight: bold;
  font-size: 1.6rem;
}

span {
  font-weight: bold;
}

label {
  font-weight: bold;
}

.group {
  margin-top: 10px;
}
</style>



