<template>
  <table class="table">
    <thead class="thead-dark">
      <tr>
        <th scope="col">#</th>
        <th scope="col">Sales Associate</th>
        <th scope="col">Customer Name</th>
        <th scope="col">Stock #</th>
        <th scope="col">Type of Vehicle</th>
        <th scope="col">Sales Status</th>
        <th scope="col">Parts Status</th>
        <th scope="col">Finance Status</th>
        <th scope="col">Car Wash Status</th>
        <th scope="col">Delivery Bay</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(req, index) in reqs" :key="index">
        <th>{{index + 1}}</th>
        <td>{{req.sales_associate}}</td>
        <td>{{req.customer_name}}</td>
        <td>{{req.stock_number}}</td>
        <td>{{ req.type_of_vehicle }}</td>

        <td class="table-warning" v-if="req.status_sales == 'In progress...'">
          <span>{{req.status_sales}}</span>
          {{ req.sales_manager }}
        </td>
        <td class="table-success" v-if="req.status_sales == 'Approved'">
          <span>{{req.status_sales}}</span>
          by {{ req.sales_manager }}
        </td>
        <td class="table-danger" v-if="req.status_sales == 'Denied'">
          <span>{{req.status_sales}}</span>
          by {{ req.sales_manager }}
        </td>

        <td class="table-warning" v-if="req.status_parts == 'Currently Waiting...'">
          <span>{{req.status_parts}} {{ elapsedTime(req) }} Min.</span>
        </td>
        <td
          class="table-success"
          v-if="(req.status_parts == 'Complete' && req.parts_on_order == 'No') || (req.status_parts == 'Complete' && !req.parts_on_order) "
        >
          <span>{{req.status_parts}}</span>
          by {{ req.parts_associate }}
        </td>
        <td class="table-primary" v-if="req.status_parts == 'Claimed'">
          <span>{{req.status_parts}}</span>
          by {{ req.parts_associate }}
        </td>
        <td class="table-success" v-if="req.status_parts == 'No Parts Needed'">
          <span>{{req.status_parts}}</span>
        </td>
        <td class="table-danger" v-if="req.parts_on_order == 'Yes' ">
          <span>Parts need to be order...Speak with {{ req.parts_associate }} for an ETA</span>
        </td>

        <td class="table-warning" v-if="req.status_finance == 'In progress...'">
          <span>{{req.status_finance}}</span>
        </td>
        <td class="table-success" v-if="req.status_finance == 'Complete'">
          <span>{{req.status_finance}}</span>
          by {{ req.finance_associate }}
        </td>
        <td class="table-primary" v-if="req.status_finance == 'Claimed'">
          <span>{{req.status_finance}}</span>
          by {{ req.finance_associate }}
        </td>

        <td class="table-warning" v-if="req.status_carwash == 'In progress...'">
          <span>{{req.status_carwash}}</span>
        </td>
        <td class="table-success" v-if="req.status_carwash == 'Complete'">
          <span>{{req.status_carwash}}</span>
          by {{ req.carwash_associate }}
        </td>
        <td class="table-primary" v-if="req.status_carwash == 'Claimed'">
          <span>{{req.status_carwash}}</span>
          by {{ req.carwash_associate }}
        </td>
        <td>{{req.delivery_bay}}</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import { db } from "../main";
import router from "../router";

export default {
  name: "LastComplete",
  data() {
    return {
      reqs: []
    };
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
        .orderBy("request_timestamp", "desc")
        .where("status", "==", "Complete")
        .limit(50)
    };
  },
  methods: {
    dateFormat(date) {
      return new Date(date.seconds * 1000).toLocaleString();
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.approved {
  background-color: green;
}

.inprogress {
  background-color: yellow;
}

.denied {
  background-color: red;
}

span {
  font-weight: bold;
  font-size: 15px;
}
</style>



