<template>
  <div class="container">
    <div class="row">
      <div class="col-sm-12">
        <h1 class="display-3">We Owe</h1>
        <p class="heading">
          This is going to take place of the paper we owe! Please fill out this form entirely. Once you submit this request it will be sent to the sales managment team for approval. If approved it will be sent to parts, where they will pull the items you have requested. You can view the status of your we owe
          <router-link to="/">here</router-link>at any time.
        </p>
      </div>
      <form
        @submit.prevent="newWeOwe(customer_name, stock_number, type_of_vehicle, make, year, model, address, city, state, zip_code, we_owe_items, ship)"
        class="col-md-12"
      >
        <div class="form-group">
          <div class="form-row">
            <div class="col-md-6">
              <label for="customer_name">Customer's First and Last Name</label>
              <input
                class="form-control form-control-lg"
                type="text"
                placeholder="Ex: John Smith"
                id="customer_name"
                v-model="customer_name"
                required
              />
            </div>
            <div class="col-md-6">
              <label for="stock_number">Stock Number</label>
              <input
                class="form-control form-control-lg"
                type="text"
                placeholder="Ex: VF456789"
                id="stock_number"
                v-model="stock_number"
                required
              />
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="form-row">
            <div class="col-md-3">
              <label for="type_of_vehicle">Type of Vehicle</label>
              <select
                id="type_of_vehicle"
                class="form-control form-control-lg"
                v-model="type_of_vehicle"
                required
              >
                <option></option>
                <option v-for="(type, i) in types" :key="i">{{ type }}</option>
              </select>
            </div>
            <div class="col-md-3">
              <label for="make">Make</label>
              <select id="make" class="form-control form-control-lg" v-model="make" required>
                <option></option>
                <option v-for="(make, i) in makes" :key="i">{{ make }}</option>
              </select>
            </div>
            <div class="col-md-3">
              <label for="year">Year</label>
              <select required id="year" class="form-control form-control-lg" v-model="year">
                <option></option>
                <option v-for="(year, i) in years" :key="i">{{ year }}</option>
              </select>
            </div>
            <div class="col-md-3">
              <label for="model">Model</label>
              <select required id="model" class="form-control form-control-lg" v-model="model">
                <option></option>
                <option v-for="(model, i) in models" :key="i">{{ model }}</option>
              </select>
            </div>
          </div>
        </div>
        <div class="form-group">
          <div>
            <label for="address">Address</label>
            <input
              class="form-control form-control-lg"
              type="text"
              placeholder="11507 IH-10 West"
              id="address"
              v-model="address"
            />
          </div>
        </div>
        <div class="form-group">
          <div class="form-row">
            <div class="col-md-4">
              <label for="city">City</label>
              <input
                class="form-control form-control-lg"
                type="text"
                placeholder="San Antonio"
                id="city"
                v-model="city"
              />
            </div>
            <div class="col-md-4">
              <label for="state">State</label>
              <input
                class="form-control form-control-lg"
                type="text"
                placeholder="Texas"
                id="state"
                v-model="state"
              />
            </div>
            <div class="col-md-4">
              <label for="zip_code">Zip Code</label>
              <input
                class="form-control form-control-lg"
                type="text"
                placeholder="78249"
                id="zip_code"
                v-model="zip_code"
              />
            </div>
          </div>
        </div>
        <div class="form-group">
          <div class="form-row">
            <div class="col-md-12">
              <label for="we_owe_items">List what this we owe is for</label>
              <textarea
                class="form-control"
                id="we_owe_items"
                rows="5"
                placeholder="Enter part numbers or description of items needed. Please add any notes that are relivant for the parts department."
                v-model="we_owe_items"
                required
              ></textarea>
            </div>
          </div>
        </div>
        <div class="form-check">
          <input class="form-check-input" type="checkbox" v-model="ship" id="ship" />
          <label class="form-check-label" for="ship">Ship to Customer</label>
          <small
            id="shipping_info"
            class="form-text text-muted"
          >Parts will box up and ship your part out to the customer. You will be charged freight!</small>
        </div>
        <div class="form-group submit-button">
          <button type="submit" class="btn btn-lg btn-success">Submit</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";
import firebase from "firebase";

export default {
  name: "SalesAssociate",
  data() {
    return {
      user: firebase.auth().currentUser.displayName,
      customer_name: "",
      stock_number: "",
      type_of_vehicle: "",
      year: "",
      make: "",
      model: "",
      address: "",
      city: "",
      state: "",
      zip_code: "",
      we_owe_items: "",
      ship: false,
      makes: ["BMW", "MINI", "Pre-Owned"],
      models: [
        "1 series",
        "2 series",
        "3 series",
        "4 series",
        "5 series",
        "6 series",
        "7 series",
        "8 series",
        "X1",
        "X2",
        "X3",
        "X4",
        "X5",
        "X6",
        "X7",
        "Z4",
        "i8",
        "i3",
        "M2",
        "M3",
        "M4",
        "M5",
        "M6",
        "M8",
        "Chrysler",
        "Dodge",
        "Jeep",
        "Ford",
        "Lincoln",
        "Buick",
        "Cadillac",
        "Chevrolet",
        "GMC",
        "Audi",
        "Lexus",
        "Land Rover",
        "Mercedes",
        "Jaguar",
        "Volvo",
        "VW",
        "Porsche",
        "Tesla",
        "Subaru",
        "Honda",
        "Nissan",
        "Toyota",
        "Kia",
        "Hyundai",
        "Fiat",
        "Other"
      ],
      years: [
        "2020",
        "2019",
        "2018",
        "2017",
        "2016",
        "2015",
        "2014",
        "2013",
        "2012",
        "2011",
        "2010",
        "2009",
        "2008",
        "2007",
        "2006",
        "2005",
        "2004",
        "2003",
        "2002",
        "2001",
        "2000"
      ],
      types: ["New", "Pre-Owned", "Loaner"]
    };
  },
  methods: {
    newWeOwe(
      customer,
      stock,
      type,
      make,
      year,
      model,
      address,
      city,
      state,
      zip,
      items,
      ship
    ) {
      db.collection("weowe").add({
        request_timestamp: new Date(),
        sales_associate: this.user,
        customer_name: customer,
        stock_number: stock,
        type_of_vehicle: type,
        make: make,
        year: year,
        model: model,
        address: address,
        city: city,
        state: state,
        zip_code: zip,
        we_owe_items: items,
        ship: ship,
        status: "Incomplete",
        sales_status: "Currently Waiting..."
      });
      this.customer_name = "";
      this.type_of_vehicle = "";
      this.year = "";
      this.make = "";
      this.model = "";
      this.address = "";
      this.city = "";
      this.state = "";
      this.zip_code = "";
      this.we_owe_items = "";
      this.ship = false;
      this.$router.push("/weowedispatch");
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.form-group,
.form-row {
  text-align: left;
}

.submit-button {
  margin-top: 20px;
  text-align: center;
}
</style>



