<template>
  <div class="container">
    <div class="heading row justify-content-center">
      <h3 class="display-3">Sales Manger View</h3>
    </div>
    <div class="link-top row justify-content-center">
      <router-link
        v-for="(sale, i) in sales"
        :key="i"
        :to="sale.path"
        class="col btn btn-lg btn-outline-primary"
      >{{ sale.title }}</router-link>
    </div>
    <div class="link row justify-content-center">
      <router-link
        v-for="(dash, i) in dashboards"
        :key="i"
        :to="dash.path"
        class="col btn btn-lg btn-outline-primary"
      >{{ dash.title }}</router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "SalesManager",
  data() {
    return {
      sales: [
        {
          title: "New Car",
          path: "/newcar",
          access: "SalesManager"
        },
        {
          title: "TPO",
          path: "/preowned",
          access: "SalesManager"
        },
        {
          title: "Loan Car",
          path: "/loancar",
          access: "SalesManager"
        }
      ],
      dashboards: [
        {
          title: "In Progress",
          path: "/metrics"
        },
        {
          title: "Last 50 Complete",
          path: "/lastcomplete"
        },
        {
          title: "Last 50 Denied",
          path: "lastdenied"
        }
      ]
    };
  }
};
</script>

<style scoped>
a {
  margin-bottom: 20px;
  margin-left: 5px;
  margin-right: 5px;
}

.container {
  height: 100%;
}

.link-top {
  margin-top: 5%;
}
</style>





