<template>
  <table class="table">
    <thead class="thead-dark">
      <tr>
        <th scope="col">#</th>
        <th scope="col">Id</th>
        <th scope="col">Sales Associate</th>
        <th scope="col">Stock #</th>
        <th scope="col">Type of Vehicle</th>
        <th scope="col">Initial Request Timestamp</th>
        <th scope="col">Sales Manager Timestamp</th>
        <th scope="col">Init to Approval (min.)</th>
        <th scope="col">Parts Claim Timestamp</th>
        <th scope="col">Approval to Parts Claim (min.)</th>
        <th scope="col">Parts Completion Timestamp</th>
        <th scope="col">Parts Claim to Parts Complete (min.)</th>
        <th scope="col">Finance Claimed Timestamp</th>
        <th scope="col">Approval to Finance Claim (min.)</th>
        <th scope="col">Finance Completion Timestamp</th>
        <th scope="col">Finance Claim to Finance Complete (min.)</th>
        <th scope="col">Detail Claimed Timestamp</th>
        <th scope="col">Approval to Detail Claimed (min.)</th>
        <th scope="col">Detail Completion Timestamp</th>
        <th scope="col">Detail Claim to Detail Complete (min.)</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(req, index) in reqs" :key="index">
        <th>{{index + 1}}</th>
        <th>{{req.id}}</th>
        <td>{{req.sales_associate}}</td>
        <td>{{req.stock_number}}</td>
        <td>{{ req.type_of_vehicle }}</td>

        <td>{{ dateFormat(req.request_timestamp) }}</td>
        <td>{{ dateFormat(req.sales_completion_timestamp) }}</td>
        <td>{{ d1(req.request_timestamp, req.sales_completion_timestamp) }}</td>
        <td>{{ dateFormat(req.parts_claimed_timestamp) }}</td>
        <td>{{ d1(req.sales_completion_timestamp, req.parts_claimed_timestamp )}}</td>
        <td>{{ dateFormat(req.parts_completion_timestamp) }}</td>
        <td>{{ d1(req.parts_claimed_timestamp, req.parts_completion_timestamp )}}</td>
        <td>{{ dateFormat(req.finance_claimed_timestamp) }}</td>
        <td>{{ d1(req.sales_completion_timestamp, req.finance_claimed_timestamp )}}</td>
        <td>{{ dateFormat(req.finance_completion_timestamp) }}</td>
        <td>{{ d1(req.finance_claimed_timestamp, req.finance_completion_timestamp )}}</td>
        <td>{{ dateFormat(req.carwash_claimed_timestamp) }}</td>
        <td>{{ d1(req.sales_completion_timestamp, req.carwash_claimed_timestamp )}}</td>
        <td>{{ dateFormat(req.carwash_complete_timestamp) }}</td>
        <td>{{ d1(req.carwash_claimed_timestamp, req.carwash_complete_timestamp )}}</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import { db } from "../main";
import router from "../router";

export default {
  name: "Dashboard",
  data() {
    return {
      reqs: []
    };
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
        .where("status", "==", "Complete")
        .orderBy("request_timestamp", "desc")
    };
  },
  methods: {
    dateFormat(date) {
      return new Date(date.seconds * 1000).toLocaleString();
    },
    d1(req, claim) {
      return Math.floor((claim.seconds - req.seconds) / 60);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.approved {
  background-color: green;
}

.inprogress {
  background-color: yellow;
}

.denied {
  background-color: red;
}

span {
  font-weight: bold;
  font-size: 15px;
}
</style>



