<template>
  <div class="container">
    <div class="row">
      <div class="col-md-12">
        <h1 class="display-3 heading">Report a Bug</h1>
      </div>
      <form class="col-sm-12">
        <div class="form-group">
          <label for="associate_name">Name</label>
          <input
            type="text"
            class="form-control"
            id="associate_name"
            placeholder="Please enter your first and last name"
            v-model="associate_name"
            required
          />
          <small
            id="emailHelp"
            class="form-text text-muted"
          >You may be contacted if more information is needed.</small>
        </div>
        <div class="form-group">
          <label for="comments">Comments</label>
          <input
            type="textarea"
            class="form-control"
            id="comments"
            placeholder="With as much detail as possible describe the bug..."
            v-model="comments"
            required
          />
        </div>
        <v-btn v-if="associate_name || comments" @click="clear()" color="error">Clear</v-btn>
        <v-btn @click="newBug(associate_name, comments)" color="success">Submit</v-btn>
      </form>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";
import { EventBus } from "../event-bus.js";

export default {
  name: "Bug",
  data() {
    return {
      bugs: [],
      associate_name: "",
      comments: ""
    };
  },
  firestore() {
    return {
      reqs: db.collection("bug_report")
    };
  },
  methods: {
    newBug(associate_name, comments) {
      db.collection("bug_report").add({
        associate_name: associate_name,
        comments: comments,
        status: "Pending"
      });
      this.associate_name = "";
      this.comments = "";
      this.$router.push("/bugs");
    },
    clear() {
      this.associate_name = "";
      this.comments = "";
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  margin-top: 20px;
  margin-bottom: 30px;
}

.form-group {
  margin-bottom: 20px;
}

button {
  margin-top: 3rem;
  margin-bottom: 50px;
  margin-left: 20px;
  height: 3.5rem;
  width: 20%;
}

label {
  font-weight: bold;
  font-size: 1.2em;
}

.form-row {
  margin-bottom: 2rem;
}

.heading {
  margin-bottom: 5rem;
  margin-top: 5rem;
}
</style>



