<template>
  <div class="container-fluid">
    <div class="sold-units">
      <h3 class="display-3">Make Ready Vehicles</h3>
    </div>
    <div class="table-responsive">
      <table class="table table-hover">
        <thead class="thead-dark">
          <tr>
            <th class="align-middle" scope="col">#</th>
            <th class="align-middle" scope="col">Timer</th>
            <th class="align-middle" scope="col">Stock #</th>
            <th class="align-middle" scope="col">Sales Associate</th>
            <th class="align-middle" scope="col">Cutomer Name</th>
            <th class="align-middle" scope="col">Type of Vehicle</th>
            <th class="align-middle" scope="col">Year</th>
            <th class="align-middle" scope="col">Model</th>
            <th class="align-middle" scope="col">Customer Care Package</th>
            <th class="align-middle" scope="col">Accessories</th>
            <th class="align-middle" scope="col">Which Accessories?</th>
            <th class="align-middle" scope="col">Additional Information</th>
            <th class="align-middle" scope="col"></th>
            <th class="align-middle" scope="col"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(req, index) in reqs" :key="index">
            <th class="align-middle">{{index + 1}}</th>
            <th class="align-middle">{{ elapsedTime(req.request_timestamp) }} Min.</th>
            <td class="align-middle">{{ req.stock_number }}</td>
            <td class="align-middle">{{ req.sales_associate }}</td>
            <td class="align-middle">{{ req.customer_name }}</td>
            <td class="align-middle">{{ req.type_of_vehicle }}</td>
            <td class="align-middle">{{ req.year }}</td>
            <td class="align-middle">{{ req.model }}</td>
            <td class="align-middle">{{ req.customer_care_package }}</td>
            <td class="align-middle">{{ req.accessories }}</td>
            <td class="align-middle">{{ req.list_accessories }}</td>
            <td class="word align-middle">{{ req.additional_comments }}</td>
            <td class="align-middle">
              <!-- Parts Associate will claim the request as there own and start working on filling the request -->
              <div class="input" v-if="req.parts_state">
                <label for="status_parts">Parts Status</label>
                <select class="form-control" v-model="req.status_parts" id="status_parts" required>
                  <option></option>
                  <option v-for="(claim, i) in claims" :key="i">{{ claim }}</option>
                </select>
              </div>
              <!-- Parts Associate will answer if this request was put in after hours or not. This will show up every time. *TODO* Have the system know if this request came in after hours. -->
              <div class="input" v-if="!req.parts_state && req.customer_care_package == 'No'">
                <label for="mats">Was this an after hours request?</label>
                <select id="mats" class="form-control form-control-lg" v-model="req.afterhours">
                  <option v-for="(approval, index) in approvals" :key="index">{{ approval }}</option>
                </select>
              </div>
              <!-- This field will show up if there isn't a customer care package -->
              <div
                class="input"
                v-if="!req.parts_state && req.customer_care_package == 'No' && req.afterhours == 'Yes' "
              >
                <label for="mats">Mats</label>
                <select id="mats" class="form-control form-control-lg" v-model="req.mats">
                  <option>Carpet Mats</option>
                </select>
              </div>

              <div class="input" v-else-if="!req.parts_state && req.afterhours == 'No'">
                <label for="mats">Mats</label>
                <select id="mats" class="form-control form-control-lg" v-model="req.mats">
                  <option v-for="(mat, index) in mats" :key="index">{{ mat }}</option>
                </select>
              </div>

              <div class="input" v-else-if="!req.parts_state">
                <label for="mats">Mats</label>
                <select id="mats" class="form-control form-control-lg" v-model="req.mats">
                  <option v-for="(mat, i) in mats" :key="i">{{ mat }}</option>
                </select>
              </div>
              <div class="input" v-if="!req.parts_state">
                <label for="parts_on_order">Did Parts Need to Be Ordered?</label>
                <select
                  id="parts_on_order"
                  class="form-control form-control-lg"
                  v-model="req.parts_on_order"
                >
                  <option v-for="(approval, i) in approvals" :key="i">{{ approval }}</option>
                </select>
              </div>
              <div class="input">
                <label v-if="req.mats == 'Customer Care Package'" for="mats">Invoice #</label>
                <input
                  v-if="req.mats == 'Customer Care Package'"
                  type="text"
                  class="form-control form-control-lg"
                  id="stock_number"
                  placeholder="ex: 123456"
                  v-model="req.invoice_number"
                  required
                />
              </div>
            </td>
            <td class="align-middle">
              <!-- Claimed Button, this is the first button the associate sees, when they click this button it will show that they have claimed the request. It will also show them more options that they need to fill out. -->
              <div v-if="req.parts_state">
                <button @click="claimed(req.id, req.status_parts)" class="btn btn-success">Claim</button>
              </div>
              <div v-if="!req.parts_state">
                <button class="btn btn-danger" @click="unclaimed(req.id)">Unclaim</button>
                <button
                  @click="complete(req, req.id, req.afterhours)"
                  class="btn btn-success"
                >Complete</button>
              </div>
              <div v-if="!req.parts_state"></div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="we-owes">
      <h3 class="display-3">We Owes</h3>
    </div>
    <div class="table-responsive">
      <table class="table table-hover">
        <thead class="thead-dark">
          <tr>
            <th class="align-middle" scope="col">#</th>
            <th class="align-middle" scope="col">Timer</th>
            <th class="align-middle" scope="col">Sales Associate</th>
            <th class="align-middle" scope="col">Customer Name</th>
            <th class="align-middle" scope="col">Stock #</th>
            <th class="align-middle" scope="col">Type of Vehicle</th>
            <th class="align-middle" scope="col">Year</th>
            <th class="align-middle" scope="col">Model</th>
            <th class="align-middle" scope="col">We Owe Items/Notes</th>
            <th class="align-middle" scope="col">Approval Status</th>
            <th class="align-middle" scope="col"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(wo, index) in weowes" :key="index">
            <th class="align-middle">{{index + 1}}</th>
            <th class="align-middle">{{ elapsedTime(wo.request_timestamp) }} Min.</th>
            <td class="align-middle">{{ wo.sales_associate }}</td>
            <td class="align-middle">{{ wo.customer_name }}</td>
            <td class="align-middle">{{ wo.stock_number }}</td>
            <td class="align-middle">{{ wo.type_of_vehicle }}</td>
            <td class="align-middle">{{ wo.year }}</td>
            <td class="align-middle">{{ wo.model }}</td>
            <td class="align-middle">{{ wo.we_owe_items }}</td>
            <td class="align-middle">
              <select class="form-control" v-model="wo.sales_status">
                <option></option>
                <option v-for="(claim, i) in claims" :key="i">{{ claim }}</option>
              </select>
            </td>
            <td class="align-middle">
              <button class="btn btn-success">Submit</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";
import firebase from "firebase";

export default {
  name: "WeOweSales",
  data() {
    return {
      weowes: [],
      reqs: [],
      claims: ["Claimed"],
      approvals: ["Yes", "No"],
      mats: ["Customer Care Package", "Carpet"],
      currentTime: Date.now(),
      state: "started",
      partsAssociate: ""
    };
  },
  firestore() {
    return {
      weowes: db
        .collection("weowe")
        .where("sales_status", "==", "Approved")
        .orderBy("request_timestamp"),
      reqs: db
        .collection("makeReady")
        .where("send_to_parts", "==", true)
        .where("status_sales", "==", "Approved")
        .orderBy("request_timestamp")
    };
  },
  methods: {
    dateFormat(date) {
      return new Date(date.seconds * 1000).toLocaleString();
    },
    elapsedTime(req) {
      const timestamp = req;
      const startTime = timestamp.seconds * 1000;
      return Math.floor((this.currentTime - startTime) / 60000);
    },
    updateCurrentTime: function() {
      if (this.$data.state == "started") {
        this.currentTime = Date.now();
      }
    },
    claimed(id, status) {
      db.collection("makeReady")
        .doc(id)
        .update({
          parts_associate: this.partsAssociate,
          parts_claimed_timestamp: new Date(),
          status_parts: status,
          parts_state: false
        });
    },
    unclaimed(id) {
      db.collection("makeReady")
        .doc(id)
        .update({
          parts_associate: "",
          parts_claimed_timestamp: "",
          status_parts: "Currently Waiting...",
          parts_state: true
        });
    },
    complete(req, id) {
      if (
        req.status_finance == "Complete" &&
        req.status_carwash == "Complete"
      ) {
        db.collection("makeReady")
          .doc(id)
          .update({
            invoice_number: req.invoice_number,
            parts_completion_timestamp: new Date(),
            status_parts: "Complete",
            mats: req.mats,
            send_to_parts: false,
            parts_on_order: req.parts_on_order,
            status: "Complete"
          });
      } else {
        db.collection("makeReady")
          .doc(id)
          .update({
            invoice_number: req.invoice_number,
            parts_completion_timestamp: new Date(),
            status_parts: "Complete",
            mats: req.mats,
            send_to_parts: false,
            parts_on_order: req.parts_on_order
          });
      }
    }
  },
  mounted: function() {
    this.interval = setInterval(this.updateCurrentTime, 1000);
    this.partsAssociate = firebase.auth().currentUser.displayName;
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container-fluid {
  margin-top: 20px;
}

.we-owes {
  margin-top: 100px;
}

.display-3 {
  text-align: left;
}

.word {
  word-break: break-word;
  width: 200px;
}

.input {
  margin-top: 5px;
}
</style>



